import { Component, H } from "../../Component";
import * as i18n from "dojo/i18n!./nls/resources";
import Tag from "./Tag";
import SuggestionDropdown from "./SuggestionDropdown";

export interface TagsProps {
    /**
     * Unique key for the component
     * @type {string}
     */
    key: string;

    /**
     * Array of applied tags.
     * @type {Array}
     * @default []
     */
    activeTags: string[];

    /**
     * Array of known tags.
     * @type {Array}
     * @default []
     */
    knownTags: string[];

    /**
     * Callback fired when a tag is added or removed.
     * @param tags - Array of currently selected tags.
     */
    onChange: (tags: string[]) => void;

    /**
     * Maximum width for the widget.
     * @type {string}
     * @default "auto"
     */
    maxWidth?: string;

    /**
     * Minimum width for the widget.
     * @type {string}
     * @default "auto"
     */
    minWidth?: string;

    /**
     * Whether a value is required for this widget.
     * @type {boolean}
     * @default false
     */
    required?: boolean;

    /**
     * If true, shows input validation in the legacy style.
     * @type {boolean}
     * @default false
     */
    legacyValidation?: string;
}

interface TagsState {
    /**
     * Current string in the input (pending addition).
     * @type {string}
     * @default ""
     */
    value: string;

    /**
     * Boolean representing whether or not the tags input is focused.
     * @type {boolean}
     * @default false
     */
    isFocused: boolean;

    /**
     * Boolean representing whether or not the user is typing in the input.
     * @type {boolean}
     * @default false
     */
    isTyping: boolean;

    /**
     * Boolean representing whether the tags input has been focused.
     * @type {boolean}
     * @default false
     */
    hasBeenFocused: boolean;
}

/**
 * A component for entering tags.
 */
export default class Tags extends Component<TagsProps, TagsState> {
    constructor(props: TagsProps) {
        super(props);

        this.state = {
            value: "",
            isFocused: false,
            isTyping: false,
            hasBeenFocused: false
        };

        this.handleInputBlur = this.handleInputBlur.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleInputFocus = this.handleInputFocus.bind(this);
        this.handleInputKeyDown = this.handleInputKeyDown.bind(this);
        this.handleSuggestionBlur = this.handleSuggestionBlur.bind(this);
        this.handleSuggestionClick = this.handleSuggestionClick.bind(this);
        this.handleSuggestionFocus = this.handleSuggestionFocus.bind(this);
        this.handleSuggestionKeyDown = this.handleSuggestionKeyDown.bind(this);
        this.handleWidgetClick = this.handleWidgetClick.bind(this);
        this.spliceTag = this.spliceTag.bind(this);
    }

    public render(tsx: H) {
        const validate = !!this.props.required &&
            this.props.activeTags.length === 0 &&
            this.state.hasBeenFocused &&
            !this.state.isTyping;

        const legacyValidate = validate && this.props.legacyValidation;

        const containerClasses = {
            "inp-tags__container": true,
            "inp-tags__container--required-legacy": legacyValidate
        };
        const ulClasses = {
            "inp-tags__active-tags": true,
            "inp-tags__active-tags--focused": this.state.isFocused,
            "inp-tags__active-tags--required": validate && !legacyValidate
        };

        const matchingTags = this.props.knownTags.filter((tagName) => {
            return (
                tagName.toLowerCase().indexOf(this.state.value.toLowerCase()) !== -1 &&
                this.props.activeTags.indexOf(tagName) === -1
            );
        });
        const maxWidth = this.props.maxWidth ? this.props.maxWidth : "auto";
        const minWidth = this.props.minWidth ? this.props.minWidth : "auto";

        return (
            <div
                id="tags-container"
                classes={containerClasses}
                style={`
                        min-width: ${minWidth};
                        max-width: ${maxWidth};
                    `}
                onclick={this.handleWidgetClick}
                aria-label={validate || legacyValidate ? i18n.required : undefined}
            >
                <ul classes={ulClasses} key="active-tags" id="active-tags">
                    {
                        this.props.activeTags.map((tagName) => (
                            <Tag name={tagName} onRemove={this.spliceTag} />
                        ))
                    }
                    <input
                        id="tags-input"
                        class="inp-tags__input"
                        placeholder={i18n.addTags}
                        value={this.state.value}
                        onkeydown={this.handleInputKeyDown}
                        onfocus={this.handleInputFocus}
                        onblur={this.handleInputBlur}
                        oninput={this.handleInputChange}
                    />
                </ul>
                {
                    validate && !legacyValidate ? (
                        <div class="inp-tags__required-tip">{i18n.required}</div>
                    ) : null
                }
                <SuggestionDropdown
                    matchingTags={matchingTags}
                    onSuggestionBlur={this.handleSuggestionBlur}
                    onSuggestionClick={this.handleSuggestionClick}
                    onSuggestionFocus={this.handleSuggestionFocus}
                    onSuggestionKeyDown={this.handleSuggestionKeyDown}
                    visible={this.state.isTyping}
                />
            </div>
        );
    }

    private addTag(input: string) {
        input.split(",").forEach((tagName) => {
            const name = tagName.trim();
            if (name && this.props.activeTags.indexOf(name) === -1) {
                this.props.onChange(this.props.activeTags.concat([name]));
            }
        });
        const el = document.getElementById("tags-input");
        if (el) {
            el.focus();
        }
    }

    private spliceTag(e: any) {
        const newActiveTags = [...this.props.activeTags];
        newActiveTags.splice(this.props.activeTags.indexOf(e.target.dataset.value), 1);
        this.props.onChange(newActiveTags);
    }

    private handleInputBlur() {
        this.setState({
            hasBeenFocused: true,
            isFocused: false,
            isTyping: false
        });
    }

    private handleInputChange(e: any) {
        this.setState({
            value: e.target.value
        });
    }

    private handleInputFocus() {
        this.setState({
            isFocused: true
        });
    }

    private handleInputKeyDown(e: any) {
        switch (e.key) {
            case "Enter":
                if (e.target.value) {
                    this.addTag(e.target.value);
                    this.setState({
                        value: "",
                        isTyping: false
                    });
                    const input = document.getElementById("tags-input");
                    if (input) {
                        input.focus();
                        input["value"] = "";
                    }
                }
                break;
            case "Tab":
                if (e.target.value) {
                    e.preventDefault();
                    this.addTag(e.target.value);
                    this.setState({
                        value: "",
                        isTyping: false
                    });
                    const input = document.getElementById("tags-input");
                    if (input) {
                        input.focus();
                        input["value"] = "";
                    }
                }
                break;
            case "Backspace":
                if (!e.target.value) {
                    const newActiveTags = [ ...this.props.activeTags ];
                    newActiveTags.pop();
                    this.props.onChange(newActiveTags);
                    this.setState({
                        value: "",
                        isTyping: false
                    });
                }
                break;
            case "Down":
            case "ArrowDown":
                const nextOption = document.getElementById("suggestion-0");
                if (this.state.isTyping && !!nextOption) {
                    e.preventDefault();
                    nextOption.focus();
                }
                break;
            default:
                this.setState({
                    isTyping: true
                });
        }
    }

    private handleSuggestionBlur(e: any) {
        e.preventDefault();
        this.setState({
            isFocused: false,
            isTyping: false
        });
    }

    private handleSuggestionClick(e: any) {
        e.preventDefault();
        this.addTag(e.target.dataset.value);
        this.setState({
            value: "",
            isFocused: true,
            isTyping: false
        });
        const input = document.getElementById("tags-input");
        if (input) {
            input.focus();
            input["value"] = "";
        }
    }

    private handleSuggestionFocus(e: any) {
        e.preventDefault();
        this.setState({
            isFocused: true,
            isTyping: true
        });
    }

    private handleSuggestionKeyDown(e: any) {
        e.preventDefault();
        switch (e.key) {
            case "Up":
            case "ArrowUp":
                if (e.target["data-index"] === 0) {
                    const el = document.getElementById("tags-input");
                    if (el) {
                        el.focus();
                    }
                } else {
                    const el = document.getElementById(`suggestion-${(e.target["data-index"] - 1)}`);
                    if (el) {
                        el.focus();
                    }
                }
                break;
            case "Down":
            case "ArrowDown":
                const nextOption = document.getElementById(`suggestion-${(e.target["data-index"] + 1)}`);
                if (nextOption) {
                    nextOption.focus();
                } else {
                    const el = document.getElementById(`suggestion-0`);
                    if (el) {
                        el.focus();
                    }
                }
                break;
            case "Tab":
                this.handleSuggestionClick(e);
                break;
            case "Enter":
                this.handleSuggestionClick(e);
                break;
            default:
            //
        }
    }

    private handleWidgetClick(e: any) {
        const el = document.getElementById("tags-input");
        if (el) {
            el.focus();
        }
    }
}
