import { H } from "../../Component";
import * as i18n from "dojo/i18n!./nls/resources";

export interface SuggestionDropdownProps {
    /**
     * Suggestions matching the input string.
     * @type {array}
     */
    matchingTags: string[];

    /**
     * Visibility of the suggestion dropdown.
     * @type {boolean}
     */
    visible: boolean;

    /**
     * Handler for when a suggestion is focused.
     * @type {function}
     */
    onSuggestionFocus?: (e?: any) => void;

    /**
     * Handler for when a suggestion loses focus.
     * @type {function}
     */
    onSuggestionBlur?: (e?: any) => void;

    /**
     * Handler for when a suggestion is selected.
     * @type {function}
     */
    onSuggestionClick?: (e?: any) => void;

    /**
     * Handler for when a key is pressed on a selection.
     * @type {function}
     */
    onSuggestionKeyDown?: (e?: any) => void;
}

/**
 * Dropdown for suggested tags.
 */
export default (
    {
        matchingTags,
        onSuggestionFocus,
        onSuggestionBlur,
        onSuggestionClick,
        onSuggestionKeyDown,
        visible
    }: SuggestionDropdownProps,
    tsx: H
) => {
    if (!visible) {
        return null;
    }

    const suggestions = matchingTags.map((tagName, index) => {
        const tagClasses = {
            "inp-tags__suggestion": true,
            "inp-tags__suggestion--grey": index % 2 === 0,
            "inp-tags__suggestion--white": index % 2 !== 0
        };
        return (
            <li
                id={`suggestion-${index}`}
                classes={tagClasses}
                data-value={tagName}
                data-index={index}
                key={tagName}
                title={tagName}
                onfocus={onSuggestionFocus}
                onblur={onSuggestionBlur}
                onmousedown={onSuggestionClick}
                onkeydown={onSuggestionKeyDown}
                tabindex="0"
            >
                {tagName}
            </li>
        );
    });
    const noMatchingTags = (
        <div class="inp-tags__no-suggestions" id="no-matching-div">
            <h5 class="inp-tags__no-suggestions-text" id="no-matching-text">{i18n.noTagsFound}</h5>
        </div>
    );
    return (
        <ul
            id="tag-suggestions"
            class="inp-tags__suggestions"
            key="tag-suggestions"
        >
            {suggestions[0] ? suggestions : noMatchingTags}
        </ul>
    );
};